import React,{useEffect,  useState} from 'react'
import {productNamesToFolders} from '../../data'
import { sellColumns, } from '../../utils/utils'
import {  Table } from "antd";
import { useParams } from 'react-router-dom'
import { createDefaultDataSource, filterBySex, fillDataSource } from './SalesTable.functions';
import Checkbox from 'antd/lib/checkbox/Checkbox';
import { filterByBrand } from '../Report/Report.functions';

export const productNames = Object.keys(productNamesToFolders);


export const SalesTable = ({salesData,currentCounterparty, currentBrand,loading}) => {
  
  const { sex } = useParams();
  const [dataSource, setDataSource] = useState(null);
  const [showSellSum, setShowSellSum] = useState(true)

  const updateDataSource = (salesData,currentBrand) => {
    let dataSource = createDefaultDataSource(sex, 0, showSellSum);
    let products = filterBySex(salesData, sex);
     products = filterByBrand(products,currentBrand)
    dataSource = fillDataSource(dataSource, productNames, products);
    setDataSource(dataSource)
  };

  useEffect(() => {

      updateDataSource(salesData,currentBrand)
        
  }, [sex,currentCounterparty,showSellSum,salesData,currentBrand]);
  return (
    <>
        <Checkbox checked={showSellSum} style={{"marginLeft":"30px"}}onChange={() => setShowSellSum((prev) => !prev)}>
        SellSum
      </Checkbox>
      <Table
        dataSource={dataSource}
        columns={sellColumns}
        columnstock="3px"
        pagination={false}
        bordered={true}
        loading={loading}
      />
    </>
  );
}



