import React, {useEffect,useState} from 'react'

import { Table } from "antd";
import {  filterByProductName,  } from '../Sales/SalesTable.functions';
import { defineGender,createDefaultDataSource, commonReportColumns, } from './CommonReport.functions';
import {  foldersToProductNames,  } from '../../data';
import { CounterpartiesReport } from '../CounterpartiesReport/CounterpartiesReport';
import { filterByBrand } from '../Report/Report.functions';


export const CommonReport = ({momentFrom, momentTo, counterparties,salesData,loading,currentBrand}) => {
    const [dataSource, setDataSource] = useState(createDefaultDataSource())
    
    function updateDataSource(salesData,currentBrand){
        const newDataSource = createDefaultDataSource()
        newDataSource.forEach((productFolder) => {
           const {categories} = productFolder
           const {total} = categories
           let filtered = filterByProductName(salesData,foldersToProductNames[productFolder.name] )
           if(currentBrand !== "all"){
             filtered = filterByBrand(filtered,currentBrand);
           }
            filtered.forEach((product) => {
              const gender = defineGender(product.assortment.name);
              categories[gender].sellQuantity += +product.sellQuantity;
              categories[gender].sellSum += +product.sellSum;
              categories[gender].profit += +product.profit;
              categories[gender].sellCostSum += +product.sellCostSum;
              total.sellQuantity += +product.sellQuantity;
              total.sellSum += +product.sellSum;
              total.profit += +product.profit;
              total.sellCostSum += +product.sellCostSum;
            });

        })
       newDataSource.forEach((productFolder) => {
        const {categories} = productFolder

           for(let categoryName in productFolder.categories){
            const profitability =
              !!productFolder.categories[categoryName].sellCostSum &&
              +(
                (productFolder.categories[categoryName].profit * 100) /
                productFolder.categories[categoryName].sellCostSum
              ).toFixed(2);
              
               categories[categoryName].sellSum = +(parseInt(categories[categoryName].sellSum) / 100).toFixed(0)
               categories[categoryName].profit = +(parseInt(categories[categoryName].profit) / 100).toFixed(0)
               categories[categoryName].profitability = !!profitability ? profitability + "%" : 0
           }
       })
        setDataSource(newDataSource);
    }
    useEffect(()=>{

        
         updateDataSource(salesData,currentBrand)
    
    },[momentTo, momentFrom, salesData, currentBrand])


    return (
        <>
        <Table
        style={{width:"70vw"}}
        dataSource={dataSource}
        columns={commonReportColumns}
        size="small"
        columnstock="3px"
        pagination={false}
        bordered={true}
        loading={loading}
        
         />
         <CounterpartiesReport counterparties={counterparties} loading={!!!counterparties.length}/>
      </>
    )
}