import React from 'react'
import {Route,NavLink} from 'react-router-dom'
import { Menu } from "antd";
import { StockTable } from './components/Stock/StockTable';
import { SalesTable } from './components/Sales/SalesTable';
import { fetchCounterparty, fetchSales } from './core/api';
import { Report } from './components/Report/Report';
import { CommonReport } from './components/CommonReport/CommonReport';
import { Filter } from './components/Filter/Filter';


const { SubMenu } = Menu;

class App extends React.Component {
  state = {
    current: null,
    counterparties:[],
    currentCounterparty:null,
    momentFrom:"",
    momentTo:"",
    currentBrand:"all",
    loading:true,
    salesData:[]
  };
 
  componentDidMount =()=>{

    const {currentCounterparty, momentFrom, momentTo} = this.state
    this.setState({loading:true})
    fetchCounterparty().then(counterparties => this.setState({ counterparties})).then(()=>{
      fetchSales(currentCounterparty, momentFrom, momentTo).then(salesData => this.setState({salesData,loading:false}))

    })
  }

componentDidUpdate =(_, prevState) => {
  const {currentCounterparty, momentFrom, momentTo} = this.state
  const counterparty = !!currentCounterparty ? currentCounterparty.counterparty.meta.href :currentCounterparty

  if (
    prevState.currentCounterparty !== currentCounterparty ||
    prevState.momentTo !== momentTo ||
    prevState.momentFrom !== momentFrom
  ) {
    this.setState({ loading: true });
    fetchSales(counterparty, momentFrom, momentTo).then((salesData) =>
      this.setState({ salesData, loading: false })
    );
  } 
}

  handleClick = e => {
    this.setState({ current: e.key });
  };

  handleMomentFromChange = (momentFrom) => {
    this.setState({momentFrom})
  }
  
  handleMomentToChange = (momentTo) => {
    this.setState({momentTo})
  }

  handleBrandChange = (currentBrand) => {
    this.setState({currentBrand})
  }

  handleCounterpartyChange = (currentCounterparty)=> this.setState({currentCounterparty})


  render() {
    const { current } = this.state;
  
    return (
      <>
        <Menu
          onClick={this.handleClick}
          selectedKeys={[current]}
          mode="horizontal"
          theme="dark"
          style={{ position: "sticky", top: "0", zIndex: "1000" }}
        >
          <SubMenu key="boys" title="Мальчики">
            <Menu.Item key="boysStock">
              <NavLink to="/stock/boys">Oстатки </NavLink>
            </Menu.Item>
            <Menu.Item key="boysSales">
              <NavLink to="/sales/boys">Продажи </NavLink>
            </Menu.Item>
            <Menu.Item key="boysBrandReport">
              <NavLink to="/report/boys/brands">Отчет по брендам </NavLink>
            </Menu.Item>
            <Menu.Item key="boysSizesReport">
              <NavLink to="/report/boys/sizes">Отчет по размерам </NavLink>
            </Menu.Item>
          </SubMenu>

          <SubMenu key="girls" title="Девочки">
            <Menu.Item key="girlsStock">
              <NavLink to="/stock/girls">Oстатки </NavLink>
            </Menu.Item>
            <Menu.Item key="girlsSales">
              <NavLink to="/sales/girls">Продажи </NavLink>
            </Menu.Item>
            <Menu.Item key="girlsBrandReport">
              <NavLink to="/report/girls/brands">Отчет по брендам </NavLink>
            </Menu.Item>
            <Menu.Item key="girlsSizesReport">
              <NavLink to="/report/girls/sizes">Отчет по размерам </NavLink>
            </Menu.Item>
          </SubMenu>
          <SubMenu key="unisex" title="Унисекс">
            <Menu.Item key="unisexStock">
              <NavLink to="/stock/unisex">Oстатки</NavLink>
            </Menu.Item>
            <Menu.Item key="unisexSales">
              <NavLink to="/sales/unisex">Продажи</NavLink>
            </Menu.Item>
            <Menu.Item key="unisexBrandReport">
              <NavLink to="/report/unisex/brands">Отчет по брендам </NavLink>
            </Menu.Item>
            <Menu.Item key="unisexSizesReport">
              <NavLink to="/report/unisex/sizes">Отчет по размерам </NavLink>
            </Menu.Item>
          </SubMenu>
          <SubMenu key="common" title="Общее">
            <Menu.Item key="commonStock">
              <NavLink to="/stock/common">Oстатки</NavLink>
            </Menu.Item>
            <Menu.Item key="commonSales">
              <NavLink to="/sales/common">Продажи</NavLink>
            </Menu.Item>
            <Menu.Item key="commonBrandReport">
              <NavLink to="/report/common/brands">Отчет по брендам </NavLink>
            </Menu.Item>
            <Menu.Item key="commonSizesReport">
              <NavLink to="/report/common/sizes">Отчет по размерам </NavLink>
            </Menu.Item>
          </SubMenu>
        </Menu>

        <Filter
          counterparties={this.state.counterparties}
          currentCounterparty={this.state.currentCounterparty}
          currentBrand={this.state.currentBrand}
          handleBrandChange={this.handleBrandChange}
          handleCounterpartyChange={this.handleCounterpartyChange}
          handleMomentFromChange={this.handleMomentFromChange}
          handleMomentToChange={this.handleMomentToChange}

        />
        <Route
          exact
          path="/stock/:sex"
          render={() => <StockTable currentBrand={this.state.currentBrand} />}
        />
          <Route
            path="/sales/:sex"
            render={() => (
              <SalesTable
                salesData={this.state.salesData}
                momentFrom={this.state.momentFrom}
                momentTo={this.state.momentTo}
                counterparties={this.state.counterparties}
                loading={this.state.loading}
                currentCounterparty={this.state.currentCounterparty}
                currentBrand={this.state.currentBrand}
                onCounterpartyChange={this.handleCounterpartyChange}
              />
            )}
          />
        <Route
          path="/report/:sex/:reportBy"
          render={() => {
            return (
              <>
                <Report
                  salesData={this.state.salesData}
                  momentFrom={this.state.momentFrom}
                  momentTo={this.state.momentTo}
                  currentBrand={this.state.currentBrand}
                  loading={this.state.loading}
                  onBrandChange={this.handleBrandChange}
                />

                <CommonReport
                  salesData={this.state.salesData}
                  momentFrom={this.state.momentFrom}
                  momentTo={this.state.momentTo}
                  counterparties={this.state.counterparties}
                  currentBrand={this.state.currentBrand}
                  loading={this.state.loading}
                />
              </>
            );
          }}
        />
      </>
    );
  }
}

export default App
