import { Table } from 'antd';
import React, {useEffect,useState} from 'react'
import { createColumns } from '../../utils/utils';

const characteristics = ["salesCount", "profit", "salesAvgCheck", "sellSum","sellCostSum","profitability"]
export function createDefaultDataSource(characteristics, counterpartiesNames){

    return characteristics.map((name,idx)=>{
    const values = counterpartiesNames.reduce((acc,counterparty) => {
        acc[counterparty] = 0
        return acc
    },{})
      return {
        key: idx,
        name,
        ...values,
        
      };
    })
  }
export const CounterpartiesReport = (props) => {
  const counterpartiesNames = [
    ...props.counterparties.map((item) => item.counterparty.name),
    "total",
  ];
  const columns = createColumns(counterpartiesNames, "counterpartiesReport");
  const [dataSource, setDataSource] = useState(
    createDefaultDataSource(characteristics, counterpartiesNames)
  );

  function updateDataSource(counterparties, counterpartiesNames) {
    const newDataSource = createDefaultDataSource(
      characteristics,
      counterpartiesNames
    );
    counterparties.forEach((counterparty) => {
      for (let key in counterparty) {
        if (newDataSource.some((item) => item.name === key)) {
          const oldObj = newDataSource.find((item) => item.name === key);
          if (key !== "salesCount") {
            oldObj[counterparty.counterparty.name] = +(
              parseInt(counterparty[key]) / 100
            ).toFixed(0);
          } else {
            oldObj[counterparty.counterparty.name] = counterparty[key];
          }
        }
      }
    });
    newDataSource.forEach((item) => {
      item.total = counterpartiesNames.reduce((acc, name) => {
        acc += item[name];
        return acc;
      }, 0);
    });
    let profitabilityObj = newDataSource.find(
      (item) => item.name === "profitability"
    );
    counterpartiesNames.forEach((name) => {
      const sellCostSumObj = newDataSource.find(
        (item) => item.name === "sellCostSum"
      );
      const profitObj = newDataSource.find((item) => item.name === "profit");
      profitabilityObj[name] =
        +((profitObj[name] * 100) / sellCostSumObj[name]).toFixed(2) + "%";
    });
    setDataSource(newDataSource);
  }

  useEffect(() => {
    updateDataSource(props.counterparties, counterpartiesNames);
  }, [props.counterparties]);

  return (
    <>
      <Table
        style={{ width: "70vw",marginTop:"1vh" }}
        dataSource={dataSource}
        columns={columns}
        size="small"
        columnstock="3px"
        pagination={false}
        bordered={true}
        loading={props.loading}
      />
    </>
  );
}