import React,{useEffect, useState} from 'react'
import { useParams } from 'react-router-dom'
import { brandsReportColumns, getSize } from '../../utils/utils';
import { createBrandsDefaultDataSource,fillDataSource,filterByBrand, formatDataSource } from './Report.functions';
import { Table } from 'antd';
import { filterBySex } from '../Sales/SalesTable.functions';
import { brands, sizes } from '../../data';

const entitiesForReport = {
    brands:brands.slice(1),
    sizes
  }
export const Report = ({
  salesData=[],
  momentFrom,
  momentTo,
  currentBrand,
  loading,
}) => {
  const { sex } = useParams();
  const { reportBy } = useParams();
  const [dataSource, setDataSource] = useState(
    createBrandsDefaultDataSource(entitiesForReport[reportBy])
  );

  const updateDataSource = async () => {
    let data = filterBySex(salesData, sex);
    let dataSource = createBrandsDefaultDataSource(entitiesForReport[reportBy]);
    if (reportBy === "brands") {
      brands.forEach((brand) => {
        const products = filterByBrand(data, brand);
        products.forEach((product) => {
          const idx = dataSource.findIndex(
            (item) => item.name === brand
          );
          dataSource = fillDataSource(dataSource, product, idx);
        });
      });
    } else if (reportBy === "sizes") {
      data = filterByBrand(data, currentBrand);
      data.forEach((product) => {
        const idx = dataSource.findIndex(
          (item) => item.name === getSize(product.assortment.name)
        );
        dataSource = fillDataSource(dataSource, product, idx);
      });
    }
    setDataSource(formatDataSource(dataSource));
  };
  useEffect(() => {
    updateDataSource(momentFrom, momentTo);
  }, [sex, momentFrom, momentTo, reportBy, currentBrand, salesData]);

  return (
    <>
      <Table
        style={{ width: "70vw" }}
        dataSource={dataSource}
        columns={brandsReportColumns}
        columnstock="3px"
        pagination={false}
        bordered={true}
        loading={loading}
      />
    </>
  );
};


