import { Button, DatePicker, Dropdown, Menu } from 'antd';
import React from 'react'
import { Route } from 'react-router';
import { brands, dateFormat } from '../../data';
import moment from 'moment';

import "./Filter.css"

const { RangePicker } = DatePicker;


export const Filter = ({
  counterparties,
  currentCounterparty,
  currentBrand,
  handleBrandChange,
  handleCounterpartyChange,
  handleMomentFromChange,
  handleMomentToChange,
}) => {
  return (
    <div
      className="filter"
    >
      <Route path={["/sales/:sex"]}>
        <Dropdown
          overlay={counterpartiesOptions(
            counterparties,
            currentCounterparty,
            handleCounterpartyChange
          )}
        >
          <Button>{!!currentCounterparty ? currentCounterparty.counterparty.name : "Counterparty"}</Button>
        </Dropdown>
      </Route>
      <Route path={["/report/:sex/sizes", "/sales/:sex", "/stock/:sex"]}>
        <Dropdown
          overlay={brandsOptions(brands, currentBrand, handleBrandChange)}
        >
          <Button>{currentBrand !== "all" ? currentBrand : "Brand"}</Button>
        </Dropdown>
      </Route>

      <Route
        path={["/report/:sex/:reportBy", "/sales/:sex"]}
        render={() => {
          return (
            <RangePicker
              onChange={(moments) => {
                handleMomentFromChange(
                  moment(moments[0]._d).format(dateFormat)
                );
                handleMomentToChange(moment(moments[1]._d).format(dateFormat));
              }}
              format={dateFormat}
              size="large"
            />
          );
        }}
      />
    </div>
  );
};

export const brandsOptions = (items,selectedItem, onChange) => {
  
    return (
    <Menu theme="dark" selectedKeys={[selectedItem]}>
      
      {items.map(item=>{
        return   (
        <Menu.Item  onClick ={()=>{onChange(item)}} key={item}>
          {item}   
      </Menu.Item>)
      })}
    
    </Menu>
   );}
  export const counterpartiesOptions = (items, currentCounterparty, onChange) => 
      (<Menu theme="dark" selectedKeys={[!!currentCounterparty ? currentCounterparty.counterparty.name :"All"]}>
        <Menu.Item
          onClick={() => {
            onChange(null)
  
          }}
          key={"All"}
        >
          {"All"}
        </Menu.Item>
        {items.map((item) => {
          return (
            <Menu.Item
              onClick={() => {
                onChange(item)
              }}
              key={item.counterparty.name}
            >
              {item.counterparty.name}
            </Menu.Item>
          );
        })}
      </Menu>
    );
    
